<template>
  <div>
    <h1></h1>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="240"></el-table-column>
      <el-table-column prop="username" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="180">
        <template slot-scope='scope'>
          <el-button
            type="text"
            size="small"
            @click="$router.push(`/admin_users/edit/${scope.row._id}`)"
            ></el-button
          >
          <el-button type="text" size="small" @click="remove(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: []
    };
  },
  methods: {
    // 
    async fetch() {
      const res = await this.$http.get("rest/admin_users");
      this.items = res.data;
    },
    // 
    async remove(row) {
      this.$confirm(` "${row.username}"`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(async () => {
        const res = await this.$http.delete(`rest/admin_users/${row._id}`);
        this.$message({
          type: "success",
          message: "!"
        });
        this.fetch();
      })
    }
  },
  created() {
    this.fetch();
  }
};
</script>

<style lang="less" scoped></style>
